#leaderboard {
    width:600px;
    height: 600px;
    overflow: auto;
    color: #fff;
    border: 2px solid lightgray;
    margin: 0 auto;
    margin-top:2%;
    text-align: center;
    font-size:2rem;
    position: relative;
}
#leaderboard ul {
    list-style: none;
    padding: 0 5% 0 5%;
    margin-top: -5%;

}
#leaderboard ul li{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-bottom: 1px solid lightgray;
}
.user-info {
    font-weight: 800;
}
#leaderboard-input {
    font-size:.8em;
}
#leaderboard nav {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height:100px;
    /* position: fixed; */
    /* width:600px; */
    /* top:0; */
}


